.movie {
  width: 400px;
  height: 200px;
  background-color: #fff;
  margin: 30px;
  padding: 20px;

  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);

  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
}

.poster {
  position: relative;
  top: -50px;
  width: 100%;
  height: auto;
  max-width: 150px;
  /* max-height: 200px; */
}

.summary {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
